.footer-wrapper {
  background: #031b34;
  margin-top: 5rem;

  .footer-heading {
    width: 100%;
    text-align: center;

    margin: 6rem auto;

    h1 {
      font-weight: 800;
      font-size: 64px;
      line-height: 75px;
    }
  }

  .footer-btn {
    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;

    margin-bottom: 10rem;
    cursor: pointer;

    p {
      font-size: 18px;
      line-height: 21px;
      color: #fff;
      word-spacing: 2px;
    }
  }

  .footer-links {
    align-items: flex-start;
    flex-wrap: wrap;
    width: 100%;
    text-align: left;
    div {
      width: 250px;
      margin: 1rem;
    }

    .footer-links-logo {
      img {
        width: 118px;
        height: 30px;

        margin-bottom: 1rem;
      }
      p {
        font-size: 12px;
        line-height: 15px;
        color: #fff;
      }
    }

    .footer-links_div {
      display: flex;
      justify-content: flex-start;
      flex-direction: column;

      h4 {
        font-size: 14px;
        line-height: 17px;
        color: #fff;
        margin-bottom: 0.9rem;
      }

      p {
        font-size: 12px;
        line-height: 15px;
        color: #fff;

        margin: 0.5rem 0;
        cursor: pointer;
      }
    }
  }

  .footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;

    p {
      font-size: 12px;
      font-family: var(--font-family);
      line-height: 15px;
      color: #fff;
    }
  }
}

@media screen and (max-width: 850px) {
  .footer-heading h1 {
    font-size: 44px;
    line-height: 50px;
  }
}

@media screen and (max-width: 550px) {
  .footer-heading h1 {
    font-size: 34px;
    line-height: 42px;
  }

  .footer-links div {
    margin: 1rem 0;
  }

  .footer-btn {
    p {
      font-size: 14px;
      line-height: 20px;
    }
  }
}

@media screen and (max-width: 400px) {
  .footer-heading {
    h1 {
      font-size: 27px;
      line-height: 38px;
    }
  }
}
