.blog-wrapper {
  .blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
    h1 {
      font-size: 62px;
      line-height: 75px;
      font-weight: 800;
    }
  }

  .blog-container {
    .blog-group-a {
      flex: 0.75;
      margin-right: 2rem;
    }
    .blog-group-b {
      flex: 1;

      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 2rem;
    }
  }
}

@media screen and (max-width: 990px) {
  .blog-container {
    flex-direction: column-reverse;
  }

  .blog-group-a {
    margin: 2rem auto;
  }

  .blog-group-a .article-wrapper {
    width: 48%;
  }

  .blog-group-a .article-img {
    height: 250px;
  }
}

@media screen and (max-width: 700px) {
    .blog-group-b {
    grid-template-columns: repeat(1, 1fr);
  }

  .blog-group-a .article-wrapper {
    width: 100%;
  }

  .blog-heading h1 {
    font-size: 46px;
    line-height: 52px;
  }
}

@media screen and (max-width: 550px) {
  .blog-heading h1 {
    font-size: 34px;
    line-height: 42px;
  }
}
