.hero-container {
  .hero-text {
    align-items: flex-start;
    flex: 1;
    margin-right: 5rem;

    h1 {
      font-weight: 800;
      font-size: 62px;
      line-height: 75px;
      letter-spacing: -0.04em;
    }

    p {
      font-weight: 400;
      font-size: 20px;
      line-height: 28px;
      margin-top: 1.5rem;
    }
  }

  .hero-form {
    width: 100%;
    margin: 2rem 0 1rem;

    input {
      padding: 10px;
      background-color: #052d56;
      color: #ffffff;
      border-radius: 5px 0px 0px 5px;
      flex: 2;
      width: 100%;
      min-height: 50px;
      font-weight: 400;
      font-size: 20px;
      line-height: 28px;
      border: 2px solid #031b34;
      padding: 0 1rem;
      outline: none;
      color: #fff;

      border-top-left-radius: 5px;
      border-bottom-left-radius: 5px;
      &:focus {
        outline: none;
      }
    }

    button {
      flex: 0.6;
      width: 100%;
      min-height: 50px;
      font-weight: 400;
      font-size: 20px;
      line-height: 28px;
      background: #ff4820;
      border: 2px solid #ff4820;
      padding: 0 1rem;
      color: #fff;
      outline: none;

      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
    }
  }

  .hero-people {
    width: 100%;
    justify-content: flex-start;
    flex-direction: row;

    margin-top: 2rem;
    img {
      width: 181.79px;
      height: 38px;
    }
    p {
      margin: 0 0 0 1rem;
      font-weight: 500;
      font-size: 12px;
      line-height: 38px;
      color: #fff;
      text-align: center;
    }
  }

  .hero-img {
    flex: 1;
    img {
      width: 100%;
      height: 100%;
    }
  }
}

@media screen and (max-width: 1050px) {
  
  .hero-container {
    flex-direction: column;
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .hero-text {
    h1 {
      font-size: 48px;
      line-height: 60px;
    }
    p {
      font-size: 16px;
      line-height: 24px;
    }
  }

  .hero-people {
    flex-direction: column;
    p {
      margin: 0;
    }
  }

  .hero-form {
    input,
    button {
      font-size: 16px;
      line-height: 24px;
    }
  }
}

@media screen and (max-width: 490px) {
  .hero-text {
    h1 {
      font-size: 36px;
      line-height: 48px;
    }

    p {
     font-size: 14px;
     line-height: 24px;
   }
  } 


  .hero-form {
    input,
    button {
      font-size: 12px;
      line-height: 16px;
    }

  } 
}
