.app-header {
  padding: 2rem 6rem;

  .nav-links {
    flex: 1;
    justify-content: flex-start;

    .nav-logo {
      margin-right: 2rem;

      img {
        width: 62.56px;
        height: 16.02px;
      }
    }

    .sign {
      justify-content: flex-end;
    }
  }

  p {
    color: #fff;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
  }

  button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #ff4820;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    border-radius: 5px;
  }

  .nav-menu {
    margin-left: 1rem;

    // display: none;
    display: none;
    position: relative;

    svg {
      cursor: pointer;
    }
    .nav-menu-container {
      justify-content: flex-end;
      align-items: flex-end;

      text-align: end;
      background: #031b34;
      padding: 2rem;
      position: absolute;
      right: 0;
      top: 40px;
      margin-top: 1rem;
      min-width: 210px;
      border-radius: 5px;
      box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);

      p {
        margin: 1rem 0;
      }

      .menu-links-sign {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1050px) {
  .links-container {
    display: none;
  }


}

@media screen and (max-width: 700px) {
  .app-header {
    padding: 2rem 4rem;
  }
}

@media screen and (max-width: 550px) {
  .app-header {
    padding: 2rem;
    .sign {
      display: none;
    }
  }

  .mav-menu-container {
    top: 20px;
  }

  .menu-links-sign {
    display: block;
  }

  .nav-menu {
    display: block;
    position: relative;
}
}
