.article-wrapper {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  background: #031B34;

  .article-img {
    width: 100%;
    height: 100%;
    background: #040c18;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .article-content {
    padding: 1rem 1.5rem;
    height: 100%;

    p {
      font-size: 11.65px;
      font-weight: 700;
      line-height: 35px;

      color: #fff;
    }

    h3 {
      font-size: 25.11px;
      font-weight: 800;
      line-height: 30.3px;

      color: #fff;
      margin-bottom: 5rem;
      cursor: pointer;
    }

    p:last-child {
        cursor: pointer;
    }
  }
}

@media screen and (max-width: 550px) {
    .article-content h3 {
        font-size: 18px;
        line-height: 25p;
    }
}